<template>
    <div id="app">
      <h1> {{ message }} </h1>
      <h4> Price Match: {{ price_match }} </h4>
      <h4> Total money available: {{ money_available }} </h4>
      <button class="btn btn-success btn-lg" @click="buyTicket" :disabled="price_match > money_available"> Buy ticket </button>
      <button class="btn btn-success btn-lg" @click="returnTicket" :disabled="tickets_bought < 1"> Return ticket </button>
      <h4> Total tickets bought: {{ tickets_bought }} </h4>
    </div>
  </template>
  
  <script>
  
  export default {
    data () {
      return {
        message: 'My first component',
        money_available: 100,
        price_match: 20,
        tickets_bought: 0
      }
    },
    methods: {
      buyTicket () {
        this.money_available -= this.price_match
        this.tickets_bought += 1
      },
      returnTicket () {
        this.money_available += this.price_match
        this.tickets_bought -= 1
      }
    }
  }
  
  </script>
  


